@tailwind base;
@tailwind components;
@tailwind utilities;
html,
body {
  overflow-x: hidden;
}
/* PC , 테블릿 가로 (해상도 768px ~ 1023px)*/
@media all and (min-width: 768px) and (max-width: 1023px) {
  html {
    font-size: 16px;
  }
}

/* 테블릿 세로 (해상도 768px ~ 1023px)*/
@media all and (min-width: 768px) and (max-width: 1023px) {
  html {
    font-size: 15px;
  }
}

/* 모바일 가로, 테블릿 세로 (해상도 480px ~ 767px)*/
@media all and (min-width: 480px) and (max-width: 767px) {
  html {
    font-size: 14px;
  }
}

/* 모바일 가로, 테블릿 세로 (해상도 ~ 479px)*/
@media all and (max-width: 479px) {
  html {
    font-size: 13px;
  }
}

@media all and (max-height: 935px) {
  #mainBox {
    top: 53%;
    width: 14rem;
    height: 72rem;
  }
}
@media all and (max-height: 840px) {
  #mainBox {
    width: 12rem;
    height: 70rem;
  }
}

@media all and (max-height: 935px) and (max-width: 1400px) {
  #mainBox {
    width: 14rem;
    height: 72rem;
  }
  #mainBg {
    min-width: 100rem;
  }
}
@media all and (max-height: 870px) and (max-width: 1700px) {
  #mainBox {
    width: 12rem;
    height: 63rem;
  }
}
@media all and (max-height: 785px) and (min-width: 650px) {
  #mainBox {
    width: 10rem;
    height: 52rem;
  }
  #catImg {
    width: 60rem;
    left: 0;
    bottom: -5%;
  }
  #balloon {
    left: auto;
    top: 30%;
    right: 5%;
    transform: translate(0, -50%);
  }
}
@media all and (max-height: 675px) and (min-width: 650px) {
  #mainBox {
    width: 8rem;
    height: 45rem;
  }
  #mainBg {
    min-width: 70rem;
  }
  #catImg {
    width: 60rem;
    left: 0;
    bottom: -25%;
  }
  #balloon {
    left: auto;
    top: 50%;
    right: 5%;
    transform: translate(0, -50%);
  }
}
@media all and (max-height: 600px) and (max-width: 1050px) {
  #mainBox {
    width: 6rem;
    height: 35rem;
  }
  #catImg {
    width: 60rem;
    left: 0;
    bottom: -25%;
  }
  #balloon {
    left: auto;
    top: 35%;
    right: 5%;
    transform: translate(0, -50%);
  }
}
@media all and (max-height: 420px) and (max-width: 1050px) {
  #mainBox {
    width: 6rem;
    height: 35rem;
  }
  html {
    font-size: 12px;
  }
  #mainBg {
    min-width: 30rem;
  }
  #catImg {
    width: 60rem;
    left: 0;
    bottom: -25%;
  }
  #balloon {
    left: auto;
    top: 50%;
    right: 5%;
    transform: translate(0, -50%);
  }
}

@media all and (max-height: 380px) and (max-width: 800px) {
  #mainBox {
    width: 6rem;
    height: 35rem;
  }
  html {
    font-size: 12px;
  }
  #mainBg {
    min-width: 30rem;
  }
  #catImg {
    width: 60rem;
    left: -20%;
    bottom: -40%;
  }
  #balloon {
    left: auto;
    top: 50%;
    right: 5%;
    transform: translate(0, -50%);
  }
}
@media all and (max-height: 285px) and (max-width: 800px) {
  #mainBox {
    width: 5rem;
    height: 28rem;
  }
  html {
    font-size: 10px;
  }
  #mainTitle > p {
    padding-top: 1rem;
    font-size: 1.6rem;
  }
  #mainBg {
    min-width: 30rem;
  }
  #catImg {
    width: 60rem;
    bottom: -50%;
  }
  #balloon {
    left: auto;
    top: 50%;
    right: 5%;
    transform: translate(0, -50%);
  }
}
@media all and (max-height: 600px) and (max-width: 1000px) {
  #sandBox {
    grid-template-columns: repeat(4, minmax(0, 1fr));
    grid-template-rows: repeat(1, minmax(0, 1fr));
    margin-top: 2rem;
    max-height: 150px;
    gap: 1rem;
  }
  #sandBox > div {
    max-height: 150px;
  }
  #sandBox > div > img {
    height: 150px;
    object-fit: cover;
  }
  #sandBox > div p:first-of-type {
    font-size: 0.9rem;
    word-break: keep-all;
    margin-bottom: 0.5rem;
    line-height: 1.3;
  }
  #sandBox > div p:last-of-type {
    font-size: 0.5rem;
    word-break: keep-all;
    margin-bottom: 0.5rem;
    width: 95%;
  }
  #sandBox > div p > br {
    display: none;
  }

  #point2_title > p {
    font-size: 1.5rem;
    transform: translateY(50px);
  }
  #point2_circle {
    margin-top: 1rem;
    transform: translateY(50px);
  }
  #point2_circle > div {
    width: 50%;
  }
  #point2_circle > div > div {
    margin-left: 0.5rem;
    margin-right: 0.5rem;
  }
  #point2_circle p {
    font-size: 1rem;
    word-break: keep-all;
    line-height: 1.3;
  }
  #point2_warrning {
    margin-top: 1rem;
  }
  #point2_warrning > p {
    transform: translateY(50px);
    font-size: 1.3rem;
  }

  #canImg {
    width: auto;
    height: 70%;
    left: 15%;
  }
  #canImg > img {
    transform: translateY(20px);
    height: 100%;
  }
  .canCon {
    top: 60%;
  }

  #point2_endTitle p {
    font-size: 1.5rem;
    transform: translateY(100px);
    /* margin-top: 1rem; */
  }
  .point2_endP2 {
    margin-top: 0.5rem;
    margin-bottom: 1rem;
  }
  #point2_endCir1 {
    transform: translateY(50px);
    flex-direction: row;
  }
  #point2_endCir2 {
    transform: translateY(50px);
  }
  #point2_endCir2 .cir {
    width: 10rem;
    height: 10rem;
  }
  #point2_endCir2 p {
    font-size: 1.3rem;
  }
  #point2_doc {
    flex-direction: row;
    margin-top: 4rem;
  }
  #point2_doc .doc {
    width: 10rem;
    height: 16rem;
  }

  #point2_endTar {
    transform: translateY(50px);
  }
  #point2_endTar > div > div .title {
    font-size: 1.8rem;
  }
  #point2_endTar > div > div p {
    font-size: 1.3rem;
  }
  #point2_endDoc {
    margin-top: 0rem;
  }
  #point2_endDoc > div > div {
    width: 10rem;
    height: auto;
  }
  #point2_endDoc p {
    margin-top: 1rem;
  }

  #point3 .title {
    font-size: 2rem;
  }
  #point3 .subTitle {
    margin-top: 0rem;
    font-size: 1rem;
  }
  #point3_sand {
    margin-top: 1rem;
  }
  #point3_warrning > p {
    margin-top: 1rem;
  }
  #point3_sand > div > div {
    width: 10rem;
    height: 10rem;
  }
  #point3_sand > div > p {
    font-size: 0.8rem;
  }

  #pointEndTitle {
    margin-bottom: 1rem;
  }
  #pointEndImg {
    margin-top: 1rem;
  }
  #Navs {
    height: 60%;
  }
}
